import React, { useState } from 'react';
import { useSpring, useInView, animated } from '@react-spring/web';
import { InvestText } from './InvestText';
import { Banner } from './Banner';
import styled from 'styled-components';
import { device } from '../../Global/Sizes';

const Wrapper = styled.div`
  background-color: #b9dddf;
  border-radius: 40px;
  display: flex;
  justify-content: space-between;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
  height: 100%;
  width: 100%;

  @media ${device.mobile} {
    flex-direction: column;
  }
`;

const GeneralWrapper = styled.div`
  position: relative;
  margin-top: 152px;
  height: 647px;
  width: 100%;

  @media ${device.laptopL} {
    height: 552px;
  }

  @media ${device.mobile} {
    height: 842px;
    margin-top: 139px;
  }
`;
export const InvestInAllComponent = () => {
  const [ref, inView] = useInView();
  const [animatedOnce, setAnimatedOnce] = useState(false);
  const animationProps = useSpring({
    from: { left: inView && !animatedOnce ? '-100%' : '0%' },
    to: { left: '0%' },
    config: { duration: 500 },
    onRest: () => {
      setAnimatedOnce(true);
    },
  });

  return (
    <GeneralWrapper>
      <animated.div
        style={{
          position: 'absolute',
          zIndex: 0,
          width: '100%',
          height: '100%',
          ...animationProps,
        }}
        ref={ref}
      >
        <Wrapper>
          <InvestText />
          <Banner />
        </Wrapper>
      </animated.div>
    </GeneralWrapper>
  );
};
