import React, { useState, useRef } from 'react';

import styled, { keyframes } from 'styled-components';
import ArrowLeftImage from '../../../assets/arrow-left-slider.png';
import ArrowRightImage from '../../../assets/arrow-right-slider.png';
import { Slick } from './Slick';
import { Posters } from './Posters';
import { device } from '../../Global/Sizes';

const moveRight = keyframes`
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px); // Перемещение вправо
  }
`;

const moveLeft = keyframes`
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-10px); // Перемещение вправо
  }
`;

const fade = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1; // Перемещение вправо
  }
`;

const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;

  @media ${device.mobile} {
    flex-direction: column;
  }
`;

const WrapperText = styled.div`
  margin-top: 222px;
  color: #f5f5f5;
  width: 478px;

  @media ${device.laptopL} {
    max-width: 426px;
    margin-top: 145px;
  }
  @media ${device.laptop} {
    max-width: 417px;
    margin-top: 133px;
  }

  @media ${device.mobile} {
    order: 2;
    max-width: 352px;
    margin-top: 36px;
  }
`;

const Span = styled.span`
  font-size: 34px;
  line-height: 44px;
  opacity: 1;
  &.animate {
    animation: ${fade} 0.5s linear infinite;
  }
`;

const SpanAndButtons = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 42px;
`;

const Buttons = styled.div`
  display: flex;
  width: 144px;
  height: 64px;
  gap: 16px;
`;

const ButtonRight = styled.button`
  background-color: transparent;
  width: 64px;
  height: 64px;
  border: none;
  outline: none;
  filter: none;
  background-image: url(${ArrowRightImage});
  background-repeat: no-repeat;
  background-position: top 50% left 50%;
  transition: filter 0.3s ease-in-out;

  &::before {
    content: '';
  }

  &.animate {
    animation: ${moveRight} 0.5s linear infinite;
  }

  &:hover {
    cursor: pointer;
    filter: drop-shadow(3px 3px 1px rgba(0, 0, 0, 0.3));
  }
`;

const ButtonLeft = styled(ButtonRight)`
  background-image: url(${ArrowLeftImage});
  &.animate {
    animation: ${moveLeft} 0.5s linear infinite;
  }
`;
export const Slider = () => {
  const [leftAnimate, setLeftAnimate] = useState(false);
  const [rightAnimate, setRightAnimate] = useState(false);
  const [animateText, setAnimateText] = useState(false);
  const [count, setCount] = useState(1);

  const sliderText = useRef(null);
  const sliderPoster = useRef(null);

  const handleClick = (e) => {
    setAnimateText(true);
    if (e.target.name === 'left') {
      setLeftAnimate(true);
      setAnimateText(true);
      sliderText?.current?.slickPrev();
      sliderPoster?.current?.slickPrev();
      if (count > 1) {
        setCount(count - 1);
      } else {
        setCount(4);
      }
      setTimeout(() => {
        setLeftAnimate(false);
        setAnimateText(false);
      }, 500);
    } else {
      setRightAnimate(true);
      setAnimateText(true);
      sliderText?.current?.slickNext();
      sliderPoster?.current?.slickNext();
      if (count < 4) {
        setCount(count + 1);
      } else {
        setCount(1);
      }
      setTimeout(() => {
        setRightAnimate(false);
        setAnimateText(false);
      }, 500);
    }
  };
  return (
    <Wrapper>
      <WrapperText>
        <Slick forwardref={sliderText} />
        <SpanAndButtons>
          <Span className={animateText ? 'animate' : ''}>{count}/4</Span>
          <Buttons>
            <ButtonLeft
              disabled={animateText}
              name='left'
              onClick={handleClick}
              className={leftAnimate ? 'animate' : ''}
            />
            <ButtonRight
              disabled={animateText}
              name='right'
              onClick={handleClick}
              className={rightAnimate ? 'animate' : ''}
            />
          </Buttons>
        </SpanAndButtons>
      </WrapperText>
      <Posters forwardref={sliderPoster} />
    </Wrapper>
  );
};
