import React from 'react';
import LogoWebp2x from '../../assets/logo@2x.webp';
import LogoWebp1x from '../../assets/logo@1x.webp';
import LogoPng2x from '../../assets/logo@2x.png';
import LogoPng1x from '../../assets/logo@1x.png';
import LogoLaptopPng1x from '../../assets/logo-laptop@1x.png';

import styled from 'styled-components';
import { device } from './Sizes';

const Picture = styled.picture`
    @media ${device.laptopL} {
      margin-left: -55px;
    }
`;
export const Logo = () => {
  return (
    <Picture>
        <source srcSet={LogoLaptopPng1x + ' 1x'} media={device.laptopL} /> 
        <source srcSet={LogoWebp2x + ' 2x'} />
        <source srcSet={LogoWebp1x + ' 1x'} />
        <source srcSet={LogoPng2x + ' 2x'} />
        <img src={LogoPng1x} alt='logo' />
    </Picture>
  )
}
