import React from 'react';
import { Logo } from '../components/Global/Logo';
import { Title } from '../components/Header/Title';
import { Subtitle } from '../components/Header/Subtitle';
import { Form } from '../components/Header/Form';

import styled from 'styled-components';
import BackgroundImage from '../assets/header-background.png';
import BackgroundImageLaptop from '../assets/header-background-laptop.png';
import BackgroundImageMobile from '../assets/header-background-mobile.png';
import { device } from '../components/Global/Sizes';

const Header = styled.header`
  background-image: url(${BackgroundImage});
  background-repeat: no-repeat;
  background-position: top 50% left 50%;

  @media ${device.laptop} {
    background-image: url(${BackgroundImageLaptop});
  }

  @media ${device.mobile} {
    background-image: url(${BackgroundImageMobile});
  }
`;

const Wrapper = styled.div`
  padding: 107px 68px 99px 22px;
  display: flex;
  flex-direction: column;

  @media ${device.laptopL} {
    padding: 60px 78px 61px 22px;
  }

  @media ${device.laptop} {
    padding: 41px 37px 64px 22px;
  }

  @media ${device.mobile} {
    padding: 54px 10px 20px 22px;
  }
`;

const LogoAndTitle = styled.div`
  display: flex;
  justify-content: space-between;

  @media ${device.mobile} {
    flex-direction: column;
  }
`;

const SubAndButtons = styled.div`
  padding-right: 64px;
  display: flex;
  justify-content: space-between;
  margin-top: 440px;

  @media ${device.laptopL} {
    padding-right: 0;
  }

  @media ${device.mobile} {
    margin-top: 342px;
    flex-direction: column;
  }
`;

export const HeaderContainer = () => {
  return (
    <Header id='request-for-beta'>
      <Wrapper>
        <LogoAndTitle>
          <Logo />
          <Title />
        </LogoAndTitle>
        <SubAndButtons>
          <Subtitle />
          <Form />
        </SubAndButtons>
      </Wrapper>
    </Header>
  );
};
