import React, { useEffect, useState } from 'react';

import ShadowClock2xWebp from '../../../assets/shadow-clock@2x.webp';
import ShadowClock1xWebp from '../../../assets/shadow-clock@1x.webp';
import ShadowClock2xPng from '../../../assets/shadow-clock@2x.png';
import ShadowClock1xPng from '../../../assets/shadow-clock@1x.png';
import ShadowCalc2xWebp from '../../../assets/shadow-calc@2x.webp';
import ShadowCalc1xWebp from '../../../assets/shadow-calc@1x.webp';
import ShadowCalc2xPng from '../../../assets/shadow-calc@2x.png';
import ShadowCalc1xPng from '../../../assets/shadow-calc@1x.png';
import ShadowDefender2xWebp from '../../../assets/shadow-defender@2x.webp';
import ShadowDefender1xWebp from '../../../assets/shadow-defender@1x.webp';
import ShadowDefender2xPng from '../../../assets/shadow-defender@2x.png';
import ShadowDefender1xPng from '../../../assets/shadow-defender@1x.png';
import ShadowCoin2xWebp from '../../../assets/shadow-coin@2x.webp';
import ShadowCoin1xWebp from '../../../assets/shadow-coin@1x.webp';
import ShadowCoin2xPng from '../../../assets/shadow-coin@2x.png';
import ShadowCoin1xPng from '../../../assets/shadow-coin@1x.png';
import styled from 'styled-components';

import { device } from '../../Global/Sizes';

const Wrapper = styled.ul`
  margin-top: 69px;
  width: 559px;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;

  @media ${device.laptopL} {
    width: 538px;
    margin-top: 42px;
  }

  @media ${device.laptop} {
    width: 471px;
    font-size: 14px;
    line-height: 21px;
  }

  @media ${device.laptop} {
    width: 100%;
  }
`;

const Li = styled.li`
  list-style: none;
  display: flex;
  align-items: center;
  gap: 17px;

  & p {
    font-weight: 700;
    text-align: justify;
    word-spacing: 4px;
  }

  @media ${device.mobile} {
    gap: 24px;
  }

  & picture {
    width: 100px;
    height: 100px;

    @media ${device.laptopL} {
      width: 88.4px;
      height: 91px;
    }
  }
`;

const calculateTexts = (windowWidth) => {
  // Здесь вы можете определить логику для количества слайдов в зависимости от ширины экрана
  if (windowWidth >= 1024) {
    return [
      'We save your time that you can spend on your favorite things',
      "We help you calculate your finances correctly so that you don't worry about the future",
      'We help you manage your funds in a safe place so that you feel calm',
      'We help you show the real power of your tokens so that you can get interest from investments',
    ];
  } else {
    return [
      "Doesn't waste your time",
      'Adjusts offers to your funds',
      'Protects your assets from anything',
      'Gives value to your assets',
    ];
  }
};

const images = [
  {
    alt: 'clock',
    source1: ShadowClock2xWebp + ' 2x',
    source2: ShadowClock1xWebp + ' 1x',
    source3: ShadowClock2xPng + ' 2x',
    source4: ShadowClock1xPng,
  },
  {
    alt: 'calculator',
    source1: ShadowCalc2xWebp + ' 2x',
    source2: ShadowCalc1xWebp + ' 1x',
    source3: ShadowCalc2xPng + ' 2x',
    source4: ShadowCalc1xPng,
  },
  {
    alt: 'defender',
    source1: ShadowDefender2xWebp + ' 2x',
    source2: ShadowDefender1xWebp + ' 1x',
    source3: ShadowDefender2xPng + ' 2x',
    source4: ShadowDefender1xPng,
  },
  {
    alt: 'coin',
    source1: ShadowCoin2xWebp + ' 2x',
    source2: ShadowCoin1xWebp + ' 1x',
    source3: ShadowCoin2xPng + ' 2x',
    source4: ShadowCoin1xPng,
  },
];

export const Grid = () => {
  const [texts, setTexts] = useState(calculateTexts(window.innerWidth));

  useEffect(() => {
    const handleResize = () => {
      const windowWidth = window.innerWidth;
      const newTexts = calculateTexts(windowWidth);
      setTexts(newTexts);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  return (
    <Wrapper>
      {images.map((image, idx) => (
        <Li key={image.alt + idx}>
          <picture>
            <source srcSet={image.source1} />
            <source srcSet={image.source2} />
            <source srcSet={image.source3} />

            <img src={image.source4} alt={image.alt} />
          </picture>

          <p>{texts[idx]}</p>
        </Li>
      ))}
    </Wrapper>
  );
};
