import React, { useState } from 'react';
import { useSpring, useInView, animated } from '@react-spring/web';
import { Banner } from './Banner';
import { KnowText } from './KnowText';

import styled from 'styled-components';
import { device } from '../../Global/Sizes';

const Wrapper = styled.div`
  background-color: #b9dddf;
  border-radius: 40px;
  display: flex;
  justify-content: space-between;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
  height: 100%;
  padding-bottom: 10.5px;
  width: 100%;

  @media ${device.laptopL} {
    padding: 0;
  }

  @media ${device.mobile} {
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 32px;
  }
`;

const GeneralWrapper = styled.div`
  position: relative;
  margin-top: 168px;
  height: 647px;
  width: 100%;

  @media ${device.laptopL} {
    height: 552px;
  }

  @media ${device.mobile} {
    height: 842px;
    margin-top: 139px;
  }
`;

export const KnowYourFriendsComponent = () => {
  const [ref, inView] = useInView();
  const [animatedOnce, setAnimatedOnce] = useState(false);
  const animationProps = useSpring({
    from: { right: inView && !animatedOnce ? '-100%' : '0%'},
    to: { right: "0%" },
    config: { duration: 500 },
    onRest: () => {
        setAnimatedOnce(true);
    },
  });
  return (
    <GeneralWrapper>
      <animated.div
        style={{ position: 'absolute', zIndex: 0, width: '100%', height: '100%', ...animationProps }}
        ref={ref}
      >
      <Wrapper>
        <Banner />
        <KnowText />
      </Wrapper>
      </animated.div>
    </GeneralWrapper>
  )
}
