import React from 'react'
import FirstBannerMain1xPng from '../../../assets/first-banner-main.png';
import SecondBannerMain1xPng from '../../../assets/second-banner-main.png';
import SecondBannerMainMobile1xPng from '../../../assets/second-banner-main-mobile.png';

import styled from 'styled-components';
import { device } from '../../Global/Sizes';

const Wrapper = styled.div`
  width: 806px;
  position: relative;

  @media ${device.laptop} {
    width: 567px;
  } 

  @media ${device.mobile} {
    width: 100%;
    margin-left: 0;
    order: -1;
  }
`;

const FirstImage = styled.img`
  position: absolute;
  z-index: 1;
  @media ${device.laptopL} {
    left: 40px;
  } 
  @media ${device.laptop} {
    left: 0;
    width: 467px;
  } 

  @media ${device.mobile} {
    left: -10px;
    width: 291px;
  } 
`;
const SecondImage = styled.picture`
  margin-top: 135px;
  margin-left: 120px;
  width: 766px;
  display: block;
  & img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
    object-fit: contain;
  }

  @media ${device.laptopL} {
    width: 608px;
    margin-left: 215px;
    margin-top: 205px;
  }

  @media ${device.laptop} {
    width: 445px;
    margin-left: 170px;
    margin-top: 160px;
  }

  @media ${device.mobile} {
    width: 216px;
    margin-top: 74px;
    margin-left: 132px;
  }
`;
export const Banners = () => {
  return (
    <Wrapper>
        <FirstImage src={FirstBannerMain1xPng} alt='banner with infographics' />
        <SecondImage>
          <source srcSet={SecondBannerMainMobile1xPng} media={device.mobile} /> 
          <img src={SecondBannerMain1xPng} alt='banner with infographics' />
        </SecondImage>
    </Wrapper>
  )
}
