import React from 'react'
import FourthBannerMain from '../../../assets/fourth-banner-main.png';
import FourthBannerMainLaptop from '../../../assets/fourth-banner-main-laptop.png';

import styled from 'styled-components';

import { device } from '../../Global/Sizes';

const Image = styled.img`
  width: 100%;
  height: 631px;
  @media ${device.laptopL} {
    width: 571px;
    height: 455px;
    margin-top: 50px;
  }
  @media ${device.laptop} {
    width: 409px;
    height: 349px;
    margin-top: 100px;
    margin-right: 23px;
  }

  @media ${device.mobile} {
    margin: 0;
    margin-left: 10px;
    width: 351px;
    height: 291px;
  }
`;
export const Banner = () => {
  return (
    <picture>
      <source srcSet={FourthBannerMainLaptop} media={device.laptop} />
      <Image src={FourthBannerMain} alt='banner with infographics' />
    </picture>
  )
}
