import React from 'react';
import FirstPoster2xWebp from '../../../assets/first-poster@2x.webp';
import FirstPoster1xWebp from '../../../assets/first-poster@1x.webp';
import FirstPoster2xPng from '../../../assets/first-poster@2x.png';
import FirstPoster1xPng from '../../../assets/first-poster@1x.png';

import SecondPoster2xWebp from '../../../assets/second-poster@2x.webp';
import SecondPoster1xWebp from '../../../assets/second-poster@1x.webp';
import SecondPoster2xPng from '../../../assets/second-poster@2x.png';
import SecondPoster1xPng from '../../../assets/second-poster@1x.png';

import ThirdPoster2xWebp from '../../../assets/third-poster@2x.webp';
import ThirdPoster1xWebp from '../../../assets/third-poster@1x.webp';
import ThirdPoster2xPng from '../../../assets/third-poster@2x.png';
import ThirdPoster1xPng from '../../../assets/third-poster@1x.png';

import FourthPoster2xWebp from '../../../assets/fourth-poster@2x.webp';
import FourthPoster1xWebp from '../../../assets/fourth-poster@1x.webp';
import FourthPoster2xPng from '../../../assets/fourth-poster@2x.png';
import FourthPoster1xPng from '../../../assets/fourth-poster@1x.png';

import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import styled from 'styled-components';
import { device } from '../../Global/Sizes';

const Wrapper = styled.div`
  width: 796px;
  margin-top: -77px;

  @media ${device.laptopL} {
    max-width: 664px;
    margin-top: -130px;
  }

  @media ${device.laptop} {
    max-width: 460px;
    margin-top: -50px;
  }

  @media ${device.mobile} {
    max-width: 350px;
    margin-top: 38px;
  }
`;

const Picture = styled.picture`
  & img {
    width: 100%;
  }
`;

export const Posters = ({ forwardref }) => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidestoShow: 1,
    SlidesToScroll: 1,
    fade: true,
    arrows: false,
    swipe: false
  };
  return (
    <Wrapper>
      <Slider ref={forwardref} {...settings}>
          <Picture>
            <source srcSet={FirstPoster2xWebp + ' 2x'} />
            <source srcSet={FirstPoster1xWebp + ' 1x'} />
            <source srcSet={FirstPoster2xPng + ' 2x'} />
            <img src={FirstPoster1xPng} alt='poster with people' />
          </Picture>
        
          <Picture>
            <source srcSet={SecondPoster2xWebp + ' 2x'} />
            <source srcSet={SecondPoster1xWebp + ' 1x'} />
            <source srcSet={SecondPoster2xPng + ' 2x'} />
            <img src={SecondPoster1xPng} alt='poster with people' />
          </Picture>
        
          <Picture>
            <source srcSet={ThirdPoster2xWebp + ' 2x'} />
            <source srcSet={ThirdPoster1xWebp + ' 1x'} />
            <source srcSet={ThirdPoster2xPng + ' 2x'} />
            <img src={ThirdPoster1xPng} alt='poster with people' />
          </Picture>
        
          <Picture>
            <source srcSet={FourthPoster2xWebp + ' 2x'} />
            <source srcSet={FourthPoster1xWebp + ' 1x'} />
            <source srcSet={FourthPoster2xPng + ' 2x'} />
            <img src={FourthPoster1xPng} alt='poster with people' />
          </Picture>
      </Slider>
    </Wrapper>
  );
};
