import React from 'react';
import ThirdBannerMain from '../../../assets/third-banner-main.png';
import ThirdBannerMainLaptop from '../../../assets/third-banner-main-laptop.png';
import ThirdBannerMainMobile from '../../../assets/third-banner-main-mobile.png';
import styled from 'styled-components';
import { device } from '../../Global/Sizes';

const Image = styled.img`
  height: 100%;
  width: 100%;
  
  @media ${device.laptopL} {
    width: 592px;
  }

  @media ${device.laptop} {
    width: 455px;
  }

  @media ${device.mobile} {
    width: 100%;
    height: 342px;
  }
`;
export const Banner = () => {
  return (
    <picture>
      <source srcSet={ThirdBannerMainMobile} media={device.mobile} />
      <source srcSet={ThirdBannerMainLaptop} media={device.laptop} />
      <Image src={ThirdBannerMain} alt='banner with infographics' />
    </picture>
  );
};
