import React, { useState } from 'react';
import styled, { keyframes } from 'styled-components';
import { device } from '../../Global/Sizes';

const data = [
  {
    question: 'Velity is an investment platform?',
    shortQuestion: 'Velity is investment platform?',
    answer: `Yes, we provide investment tools and offers available for
  decentralized and free finance in such protocols as (Uniswap,
  Compound, Aave, Curve etc), which will help you to properly dispose of
  your coins. Our main slogan is that it 's time to manage
  cryptocurrency , not buy it !`,
  },
  {
    question: 'Are you centralized and will you ask me to do KYC?',
    shortQuestion: 'Do you ask me to do KYC?',
    answer: `No, we do not want to build some kind of complex entry threshold due
    to centralization, which entails KYC verification so that only some
    persons can get access to convenient tools. Our mission is to provide
    financial opportunities for everyone and to do it in a simple way.`,
  },
  {
    question: 'How can I be sure that all of us investments are protected?',
    shortQuestion: 'How all of my investments are protected?',
    answer: `As for your funds, we do not store and therefore cannot steal your
    funds in any way, we help you make all investments from your own
    wallet that you connect (Metamask, ConnectWallet, TrustWallet and
    etc.). We carry out a thorough inspection of the security of our
    application, which goes through the stages of auditing smart contracts
    of audit companies, as well as inspection of the safety and
    reliability of the code.`,
  },
  {
    question: 'Why do I need Velity if I can find where to invest my crypto?',
    shortQuestion: 'Why do I need use Velity?',
    answer: `We do not force you to use our platform and make investment earnings
    with the help of us. We want to solve the problem of a complex
    interface of decentralized applications that bring free and large
    income for everyone, but at the same time extensive and requiring a
    lot of information to interact with it, and therefore we strive to
    give you a clear, convenient and necessary interface that will allow
    you to reduce this time and learn how to use this ecosystem through
    practice understandable to everyone`,
  },
];

const rotateVerticalLine = keyframes`
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(450deg);
  }
`;

const rotateVerticalLineReverse = keyframes`
  0% {
    transform: rotate(450deg);
  }
  100% {
    transform: rotate(0deg);
  }
`;

const Wrapper = styled.ul`
  list-style-type: none;
  margin-top: 170px;

  @media ${device.mobile} {
    margin-top: 71px;
  }
`;

const Question = styled.p`
  font-size: 25px;
  line-height: 37.5px;
  color: #090a0c;

  @media ${device.mobile} {
    font-size: 14px;
    line-height: 21px;
  }
`;

const Answer = styled(Question)`
  font-size: 19px;
  line-height: 26.6px;
  display: ${(props) => (props.isAnimating ? 'block' : 'none')};
  opacity: 0;
  transition: opacity .5s;
  &.visible {
    opacity: 1;
  }
  width: 845px;

  @media ${device.laptop} {
    width: 700px;
}
@media ${device.mobile} {
  width: calc(100% - 30px);
  font-size: 14px;
  line-height: 21px;
}
`;

const ImgWrapper = styled.div`
  width: 27px;
  height: 27px;
  cursor: pointer;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.2);
  }

  .vertical-line {
    transition: transform 0.8s;
    transform: rotate(0deg);
    transform-origin: 50% 50%;
    animation: ${(props) =>
      props.isAnimating
        ? rotateVerticalLine
        : props.isReversed
        ? rotateVerticalLineReverse
        : 'none'}
    0.8s forwards;
  }
`;

const Li = styled.li`
  padding-bottom: 43px;
  &:last-child {
    padding-bottom: 0;
  }
`;
const QAndIcon = styled.div`
  display: flex;
  justify-content: space-between;
`;

export const QnA = () => {
  const [isAnimating, setIsAnimating] = useState(
    Array(data.length).fill(false)
  );

  const [isReversed, setIsReversed] = useState(Array(data.length).fill(false));

  const handleClick = (index) => {
    const updatedIsAnimating = [...isAnimating];
    const updatedIsReversed = [...isReversed];

    updatedIsAnimating[index] = !updatedIsAnimating[index];
    updatedIsReversed[index] = updatedIsAnimating[index] ? false : true;

    setIsAnimating(updatedIsAnimating);
    setIsReversed(updatedIsReversed);
  };

  return (
    <Wrapper>
      {data.map((item, index) => (
        <Li key={index}>
          <QAndIcon>
            <Question>{window.innerWidth >= 1024 ? item.question : item.shortQuestion}</Question>
            <ImgWrapper
              isAnimating={isAnimating[index]}
              isReversed={isReversed[index]}
              onClick={() => handleClick(index)}
            >
              <svg
                xmlns='http://www.w3.org/2000/svg'
                width='27'
                height='27'
                viewBox='0 0 160 160'
              >
                <rect
                  className='vertical-line'
                  x='68'
                  width='24'
                  height='160'
                  ry='12'
                />
                <rect
                  className='horizontal-line'
                  y='70'
                  width='160'
                  height='24'
                  rx='12'
                />
              </svg>
            </ImgWrapper>
          </QAndIcon>
          <Answer className={isAnimating ? 'visible' : ''} isAnimating={isAnimating[index]}>{item.answer}</Answer>
        </Li>
      ))}
    </Wrapper>
  );
};
