import React from 'react'
import SupportedIntegrations from '../../../assets/supported-integrations.png';
import SupportedIntegrationsLaptop from '../../../assets/supported-integrations-laptop.png';
import SupportedIntegrationsMobile from '../../../assets/supported-integrations-mobile.png';
import styled from 'styled-components';

import { device } from '../../Global/Sizes';
const Image = styled.img`
  width: 745px;

  @media ${device.laptop} {
    width: 440px;
  }

  @media ${device.mobile} {
    width: 357px;
    margin-top: 33px;
  }
`;
export const SupportedCrypto = () => {
  return (
    <picture>
      <source srcSet={SupportedIntegrationsMobile} media={device.mobile} />
      <source srcSet={SupportedIntegrationsLaptop} media={device.laptop} />
      <Image src={SupportedIntegrations} alt='supported integrations' />
    </picture>
  )
}
