import React from 'react'
import IdCards from '../../../assets/id-cards.png';
import IdCardsLaptop from '../../../assets/id-cards-laptop.png';
import styled from 'styled-components';
import { device } from '../../Global/Sizes';

const Image = styled.img`
  width: 100%;

  @media ${device.laptopL} {
    width: 552px;
    height: 490px;
  }

  @media ${device.laptop} {
    width: 388px;
    height: 426px;
  }

  @media ${device.mobile} {
    width: 271px;
    height: 322px;
    margin: 0 50px;
  }
`;
export const Banner = () => {
  return (
    <picture>
        <source srcSet={IdCardsLaptop} media={device.laptop} /> 
        <Image src={IdCards} alt='id cards' />
    </picture>
  )
}
