import React, { useEffect, useState } from 'react';
import FingerWithDots2xWebp from '../../../assets/finger-with-dots@2x.webp';
import FingerWithDots1xWebp from '../../../assets/finger-with-dots@1x.webp';
import FingerWithDots2xPng from '../../../assets/finger-with-dots@2x.png';
import FingerWithDots1xPng from '../../../assets/finger-with-dots@2x.png';
import Defender2xWebp from '../../../assets/defender@2x.webp';
import Defender1xWebp from '../../../assets/defender@1x.webp';
import Defender2xPng from '../../../assets/defender@2x.png';
import Defender1xPng from '../../../assets/defender@1x.png';
import Clock2xWebp from '../../../assets/clock@2x.webp';
import Clock1xWebp from '../../../assets/clock@1x.webp';
import Clock2xPng from '../../../assets/clock@2x.png';
import Clock1xPng from '../../../assets/clock@1x.png';
import TwoArrows2xWebp from '../../../assets/two-arrows@2x.webp';
import TwoArrows1xWebp from '../../../assets/two-arrows@1x.webp';
import TwoArrows2xPng from '../../../assets/two-arrows@2x.png';
import TwoArrows1xPng from '../../../assets/two-arrows@1x.png';

import styled from 'styled-components';

import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { device } from '../../Global/Sizes';


const Wrapper = styled(Slider)`
  margin-top: 72px;
  width: 1400px;
  @media ${device.laptopL} {
    width: 1304px;
  }

  @media ${device.laptop} {
    width: calc(100% - 26px);
  }
  & .slick-slide {
    margin-right: 24px;
    width: 332px;
    @media ${device.laptopL} {
      width: 308px;
    }

    @media ${device.laptop} {
      width: 383px;
    }

    @media ${device.mobile} {
      width: 340px;
    }
  }

  & .slick-list {
    margin-right: -24px;
  }

`;

const Card = styled.div`
  width: 100%;
  height: 272px !important;
  background-color: #B7CDE5;
  display: flex !important;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 24px;
  padding: 32px 24px 32px 48px;
  font-size: 18px;
  line-height: 25.2px;
  
  & p {
    font-weight: 400;
  }
  @media ${device.laptopL} {
    font-size: 17px;
    line-height: 23.8px;
  }

  @media ${device.laptop} {
    height: 383px !important;
  }

  @media ${device.mobile} {
    height: 340px !important;
  }
`;

const BlackCard = styled(Card)`
  background-color: #191A1B;
  color: #9A9A9A;
`;

const calculateSlides = (windowWidth) => {
  // Здесь вы можете определить логику для количества слайдов в зависимости от ширины экрана
  if (windowWidth >= 1440) {
    return { slidesToShow: 4, slidesToScroll: 0, autoPlay: false };
  } else if (windowWidth >= 1024) {
    return { slidesToShow: 2, slidesToScroll: 1, autoPlay: true };
  } else {
    return { slidesToShow: 1, slidesToScroll: 1, autoPlay: true };
  }
};

export const Grid = () => {
  const [slides, setSlides] = useState(calculateSlides(window.innerWidth));

  useEffect(() => {
    const handleResize = () => {
      const windowWidth = window.innerWidth;
      const newSlides = calculateSlides(windowWidth);
      setSlides(newSlides);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const settings = {
    dots: false,
    infinite: true,
    speed: 1000,
    slidesToShow: slides.slidesToShow,
    slidesToScroll: slides.slidesToScroll,
    arrows: false,
    autoplay: slides.autoPlay,
    autoplaySpeed: 3000,
    variableWidth: true,
  };  

  return (
    <Wrapper>
      <Slider {...settings}>
        <Card>
          <picture>
            <source srcSet={FingerWithDots2xWebp + ' 2x'} />
            <source srcSet={FingerWithDots1xWebp + ' 1x'} />
            <source srcSet={FingerWithDots2xPng + ' 2x'} />
            <img src={FingerWithDots1xPng} alt='finger clicking dots' />
          </picture>
          <p>
            We give you financial offers that are right for you. You just have
            to choose the best!
          </p>
        </Card>
        <Card>
          <picture>
            <source srcSet={Defender2xWebp + ' 2x'} />
            <source srcSet={Defender1xWebp + ' 1x'} />
            <source srcSet={Defender2xPng + ' 2x'} />

            <img src={Defender1xPng} alt='defender' />
          </picture>
          <p>
            Then this offer is confirmed and distributed through smart contracts
            according to the necessary protocols directly from your wallet
          </p>
        </Card>
        <BlackCard>
          <picture>
            <source srcSet={Clock2xWebp + ' 2x'} />
            <source srcSet={Clock1xWebp + ' 1x'} />
            <source srcSet={Clock2xPng + ' 2x'} />

            <img src={Clock1xPng} alt='clock' />
          </picture>
          <p>
            After that, we will notify you and will accompany your investment
            and give you the most necessary data about it
          </p>
        </BlackCard>
        <BlackCard>
          <picture>
            <source srcSet={TwoArrows2xWebp + ' 2x'} />
            <source srcSet={TwoArrows1xWebp + ' 1x'} />
            <source srcSet={TwoArrows2xPng + ' 2x'} />

            <img src={TwoArrows1xPng + ' 1x'} alt='two arrows' />
          </picture>
          <p>
            Smart contracts work autonomously and therefore you will be able to
            exit any investment in just one click if you find a better offer
          </p>
        </BlackCard>
      </Slider>
    </Wrapper>
  );
};
