import React from 'react'
import Poster from '../../../assets/fifth-banner.main.png';
import PosterLaptop from '../../../assets/fifth-banner-main-laptop.png';
import styled from 'styled-components';
import { device } from '../../Global/Sizes';

const Image = styled.img`
  width: 100%;
  margin-left: 100px;

  @media ${device.laptopL} {
    width: 552px;
    height: 490px;
  }

  @media ${device.laptop} {
    width: 388px;
    height: 426px;
  }

  @media ${device.mobile} {
    width: 351px;
    height: 315px;
    margin-left: 10px;
  }
`;
export const Banner = () => {
  return (
    <picture>
        <source srcSet={PosterLaptop} media={device.laptop} /> 
        <Image src={Poster} alt='id cards' />
    </picture>
  )
}
